*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: grid;
  grid-template-rows: 40px auto 30px;
  background-image: url("~/assets/numbers.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  @media #{$break1} {
    grid-template-rows: 40px auto 200px;
  }
}
